.modal-background {
  /* border: 1px solid blue; */
  background: black;
  opacity: 0.5;
  width: 100%;
  height: 100vh;
  z-index: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
}

.modal-content {
  /* border: 1px solid green; */
  width: 100%;
  min-width: 15rem;
  max-width: 25rem;
  border-radius: 0.5rem;
  padding: 0.5rem;
  position: fixed;
  background: white;
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.5);
  top: 50%;
  left: 50%;
  z-index: 10;
  transform: translate(-50%, -50%);

  display: flex;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
}
